.skillfulai-usecases {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: var(--darkblue, #030046);
   
}

.skillfulai_usecases-content {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-image: url('../../assets/usecases_back.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: relative; /* Set to relative to position the tokenomics-back2 image */
    z-index: 1;
}


.usecases-container {
    flex: 1.5;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    padding: 2rem 0;
    position: relative;
    width: 100%;
    max-width: 1200px;
    z-index: 3; /* Ensure it's above the tokenomics-back2 image */
}

.usecases-heading {
    font-family: Red Hat Display;
    text-align: left;
    margin-right: 5rem;
    position: relative;
    z-index: 2;
   
}

.usecases-heading h1 {
    color: #FFF;
   
    background-clip: text;
    font-family: Red Hat Display;
    font-size: 60px;
    font-style: normal;
    font-weight: 800;
    line-height: 70px;
    text-transform: capitalize;
    width: 600px;
    height: 83px;
    flex-shrink: 0;
   
    position: relative; /* Set to relative so it doesn't push other elements */
    z-index: 3; /* Ensure it's above the tokenomics-back2 image */
    margin-left: 0%;
    margin-top: -2%;
}



.usecases-image1 {
    
    margin-top: 2%;
    right: 0%;
    position: relative; /* Set to relative to ensure it's not affected by the header's margin */
    z-index: 2; /* Ensure it's above the tokenomics-back2 image but below the header */
}





@media screen and (max-width: 990px) {

    .usecases-heading h1 {
        font-size: 50px;
        line-height: 58px;
        width: 80%; 
        margin-left: 2.5%;
    }

    .usecases-image1 {
        margin-top: 0;
    }

}

@media screen and (max-width: 700px) {

    .usecases-heading h1 {
        font-size: 40px;
        line-height: 46px;
        width: 90%;
        margin-left: 1.5%;
    }

    .usecases-container {
        padding: 1.5rem 0;
    }

    .usecases-image1 {
        margin-top: 1.5%;
    }

}

@media screen and (max-width: 550px) {

    .usecases-heading h1 {
        font-size: 35px;
        line-height: 40px;
        width: 100%;
        margin-left: 1rem;
    }

    .usecases-container {
        padding: 1rem 0;
        width: 100vw;
    }

    .usecases-image1 {
        margin-top: 1%;
        width: 100vw;
    }

    .skillfulai-usecases {
    width: 100vw;
    }

}

